import React from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import utils
import util from '../../util';

//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

const Contact = (props) => {

    const onSubmitContact = async(e) => {
        e.preventDefault();
        try {
            let form = document.getElementById('contactUs');
            let payload = new FormData(form);
            await axios.post('/api/interaction/contact', payload);
            form.reset();
            util.notify(props.dispatch, "Message Sent!", "is-success");
        } catch (err) {
            err.response
                ? util.notify(props.dispatch, err.response.data.msg, "is-danger")
                : console.log(err);
        }
    }

    return (
        <section className="hero contact">
            <div className="hero-body">
                <div className="container has-text-centered">
                    <div className="columns is-8 is-variable ">
                        <div className="column is-two-thirds has-text-left">
                            <h1 className="title is-3">Contact Us</h1>
                            <p className="is-size-5">We are here to help and answer any question that you
                                might have. We look forward to hearing from you. Feel free to contact us via the
                                contact form or drops us an email to hello@wingmait.com</p>
                            <br/>
                            <p className="is-size-5">Follow us on social media</p>
                            <div className="social">
                                <a
                                    href="https://www.facebook.com/wingmait/"
                                    target="_blank"
                                    rel="noopener noreferrer">
                                    <img
                                        src="https://wingmait-public.s3.ap-south-1.amazonaws.com/facebook.png"
                                        alt="facebook"
                                        width="30px"
                                        height="30px"/>
                                </a>
                                <a
                                    href="https://www.instagram.com/wingmait/"
                                    target="_blank"
                                    rel="noopener noreferrer">
                                    <img
                                        src="https://wingmait-public.s3.ap-south-1.amazonaws.com/insta.png"
                                        alt="insta"
                                        width="30px"
                                        height="30px"/>
                                </a>
                                <a
                                    href="https://twitter.com/WingMait"
                                    target="_blank"
                                    rel="noopener noreferrer">
                                    <img
                                        src="https://wingmait-public.s3.ap-south-1.amazonaws.com/twitter.png"
                                        alt="twitter"
                                        width="30px"
                                        height="30px"/>
                                </a>
                                <a
                                    href="https://www.linkedin.com/company/wingmait/"
                                    target="_blank"
                                    rel="noopener noreferrer">
                                    <img
                                        src="https://wingmait-public.s3.ap-south-1.amazonaws.com/linkedin.png"
                                        alt="linkedin"
                                        width="30px"
                                        height="30px"/>
                                </a>
                                <a
                                    href="https://www.youtube.com/channel/UCLT6Bjk9Nbb9LfIpoRhH6ew"
                                    target="_blank"
                                    rel="noopener noreferrer">
                                    <img
                                        src="https://wingmait-public.s3.ap-south-1.amazonaws.com/youtube.png"
                                        alt="youtube"
                                        width="30px"
                                        height="30px"/>
                                </a>
                            </div>
                        </div>
                        <div className="column is-one-third has-text-left">
                            <form
                                id="contactUs"
                                className="form"
                                onSubmit={onSubmitContact}
                                encType="multipart/form-data">
                                <div className="field">
                                    <label className="label">Name</label>
                                    <div className="control">
                                        <input name="name" className="input is-medium" type="text" required/></div>
                                </div>
                                <div className="field">
                                    <label className="label">Email</label>
                                    <div className="control">
                                        <input name="email" className="input is-medium" type="email" required/></div>
                                </div>
                                <div className="field">
                                    <label className="label">Message</label>
                                    <div className="control">
                                        <textarea name="message" className="textarea is-medium" required></textarea>
                                    </div>
                                </div>
                                <div className="control">
                                    <button
                                        type="submit"
                                        className="button is-link is-fullwidth has-text-weight-medium is-medium">Send Message</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Contact);