//Import dependencies
import React from 'react';
import {connect} from 'react-redux';
import 'bulma';
import './index.scss';

//Import actions & auth
import actions from '../../state/actions';

//Map State to Props
const mapStateToProps = (state) => {
    return {notifications: state.notificationReducer}
}

//Build container
const Notifications = (props) => {

    return (
        <section className="notifications">
            {props
                .notifications
                .map(notification => <div className={`notification ${notification[2]}`} key={notification[0]}>
                    <button
                        className="delete"
                        onClick={(e) => {
                        e.preventDefault();
                        props.dispatch(actions.removeNotification(notification[0]))
                    }}/>{notification[1]}</div>)}
        </section>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(Notifications);