import React, {useState, useEffect, Fragment} from 'react';
import axios from 'axios';
import 'bulma';
import './index.scss'

export default() => {
    const [courses,
        setCourses] = useState([]);
    useEffect(() => {
        const main = async() => {
            const res = await axios.get('/api/course/all');
            const data = res.data.payload;
            setCourses(data);
        }
        main();
    }, [])

    return (
        <Fragment>
            <section className="home">
                <div className="container">
                    <p>All Courses</p>
                    <ul>
                        {courses.map((course, index) => <li key={index}><a href={`/course/${course.titleId}`}>{course.title}</a></li>)}
                    </ul>
                </div>
            </section>
        </Fragment>
    )
}